// strip

div.strip {
  padding: ($spacing*1.5) 0;
  background: white;
  @include bp-m {padding: ($spacing*2) 0;}

  &.alt {padding-top: 0;}

  &.section {
    padding: ($spacing*2) 0 ($spacing*1);

    @include bp-m {
      padding: ($spacing*3) 0 ($spacing*2);
      &.latest p.lead span {display: block;}
    }

    div.wrap > h2 {@extend .h1;}
    &[class*="style-"]:not(.style-5) p.lead a {color: white;}
  }

  &.style-1,
  &.style-2,
  &.style-3,
  &.style-4,
  &.style-5 {
    position: relative;
    color: white;

    &:before {
      position: absolute;
      top: 0; left: 0; right: 0; bottom: 0;
      z-index: 1;
      @extend .aesthetic;
      background-repeat: repeat;
      background-position: 50%;
      background-size: cover;
      content: "";
      opacity: 0.25;
    }

    &.faqs:before {
      background-size: 100% auto;
      background-position: 50% 0;
    }

    &:after {
      position: absolute;
      top: 0; left: 0; right: 0; bottom: 0;
      z-index: 1;
      background-image: linear-gradient(to bottom, rgba(black,0.2) 0%, transparent 60%);
      content: "";
    }

    div.wrap {
      position: relative;
      z-index: 2;
    }
  }

  &.style-1 {background: $clr-b1;}
  &.style-2 {background: $clr-b2;} 
  &.style-3 {background: $clr-b3;} 
  &.style-4 {background: $clr-b4;} 
  &.style-5 {
    background: $clr-b5;
    color: $clr-1;

    &:after {content: none;}
  } 

  &.style-2,
  &.style-4 {
    &:before {transform: rotate(180deg);}
  }

  // getting started

  &.started {
    text-align: center;
    p {@extend .align-middle;}
  }

  // about success

  &.about-success {
    @include bp-s {text-align: right;}

    div.wrap {
      position: relative;
      z-index: 3;
    }
    
    ul.checked {      
      @include bp-s {
        margin-left: auto;

        li {
          padding-left: 0;
          padding-right: 1.4em;
          text-align: right;

          &:before {left: auto; right: 0;}
        }
      }

      @include bp-l {font-size: $fs-25;}
    }

    figure {
      position: relative;
      z-index: 2;
      margin-bottom: 0;
      padding: 0 3rem;

      @include bp-s {
        margin-top: -2rem;
        padding: 27.5% 0 0;
      }

      @include bp-m {margin-top: -4rem;}
      @include bp-l {margin-top: -5rem;}

      img {
        display: block;

        @include bp-s {
          position: absolute;
          top: 0; left: 0;
        }
      }
    }
  }

  // contact - just for contact pages

  body.contact & {
    @include bp-m {div.row > div:first-child {order: 2;}}
  }

  // call to actions

  &.cta {
    position: relative;
    z-index: 50;
    padding: ($spacing*1.5) 0;
    background-color: $clr-2;
    text-align: center;
    color: white;

    @include bp-m {text-align: left;}

    div.wrap {

      @include bp-m {
        display: flex;
        justify-content: center;
        align-items: center;
      }

      @include bp-l {align-items: flex-end;}

      > h2 {
        @extend .lead;
        margin: 0;

        @include bp-m {
          flex: 0 1 auto;
          align-self: center;
        }

        span {
          font-size: $fs-20;
          
          span {
            @include bp-s {white-space: nowrap;}
          }
        }
      }

      > p {
        margin: $vmargin auto 0;

        @include bp-m {
          flex: 0 0 auto;
          align-self: center;
          margin: 0 0 0 $spacing;
        }

        a.button {
          @include bp-s-less {display: block;}
        }
      }
    }
    
    @include bp-l {
      &.spr-matcher,
      &.whitepaper-1,
      &.whitepaper-2,
      &.whitepaper-3 {

        span.image {
          flex: 0 0 auto;
          display: block;
          position: relative;
          width: 280px;
        
          &:before {
            position: absolute;
            left: -($spacing); bottom: -($spacing*1.5);
            padding-top: 69%;
            width: 100%;
            background: no-repeat 50% 100%;
            background-size: 100% auto;
            content: "";
          }
        }

        div.wrap h2 {flex: 1 1 auto;}
      }

      &.spr-matcher span.image:before {background-image: url(../../assets/images/content/cta-strips/spr-matcher.png);}
      &.whitepaper-1 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-1.png);}
      &.whitepaper-2 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-2.png);}
      &.whitepaper-3 span.image:before {background-image: url(../../assets/images/content/cta-strips/whitepaper-3.png);}
    }

    &.getting-started {
      text-align: center;

      div.wrap {
        display: block;

        h2 {@extend .h1;}
        p {margin: $vmargin auto;}
        ul {margin: $vmargin 0 0;}
      }
    }

    &.contact {
      text-align: center;

      div.wrap {display: block;} /* required reset */

      div.col {

        h2 {@extend .lead;}

        label {@include bp-s-less {display: block;}}

        > p {
          @include bp-m {margin: 0;}

          // a.icon-phone {
          //   @extend .h2;
          //   text-decoration: none;
          //   color: white;

          //   &:before,
          //   span {
          //     display: inline-block;
          //     vertical-align: middle;
          //   }

          //   &:before {
          //     margin-right: 0.4rem;
          //     border-radius: 3rem;
          //     padding: 0.3rem;
          //     background: $clr-b1;
          //     font-size: $fs-16;

          //     @include bp-s {
          //       margin-right: 0.5rem;
          //       padding: 0.5rem;
          //       font-size: $fs-25;
          //     }
          //   }
          // }
        }
      }
    }

    &.newsletters {
      div.compact-form {
        margin: 0;

        fieldset {
          @include bp-m {
            display: flex;
            align-items: center;
          }

          > p {
            @extend .h3;
            font-weight: 300;

            @include bp-m {
              flex: 0 1 auto;
              align-self: center;
              margin: 0;
              max-width: 60%; // IE 11 bug fix to make the container shrink on smaller screens
            }
          }

          div.form-group {
            margin: 0 auto;
            max-width: 520px;
            
            @include bp-m {
              flex: 1 1 auto;
              margin: 0 0 0 $spacing;
              min-width: 450px;
            }
          }
        }
      }
    }
  }
}

// page intro

div.page-intro {
  padding: ($spacing*4) 0 ($spacing*2);
  text-align: center;

  @include bp-m {padding: ($spacing*5.5) 0 ($spacing*2.5);}

  body.about & {
    @include bp-m-less {padding-bottom: 0;}
  }

  h1 {
    margin-bottom: 0;
    @include bp-l {font-size: $fs-70;}

    body.error & {
      @extend .h2;
      span {
        display: block;
        font-size: $fs-70;
        @include bp-m {font-size: $fs-100;}
      }
    }
  }

  p.lead {
    margin: $vmargin auto 0;

    span {@include bp-m {display: block;}}
    @include bp-m {max-width: 1000px;}
  }

  p.cta {margin: $vmargin 0 0;}
}

// consulting & events intro

div.consulting-intro,
div.events-intro {
  @extend .page-intro;

  &:before {background-position: 0 20%;}

  h1 {

    span.hierarchy {
      display: block;
      line-height: 1rem;

      strong {
        display: inline-block;
        margin-bottom: 1rem;
        border-radius: 2rem;
        padding: 0.35rem 1rem;
        background: rgba(black,0.3);
        color: white;
        font-size: $fs-16;
        font-weight: 400;

        @include bp-m {font-size: $fs-18;}
      }
    }

    span {
      @include bp-m {display: block;}
    }
  }
}

// guide intro

div.guide-intro {
  @extend .page-intro;

  @include bp-m {padding-bottom: $spacing*3;}

  &:before {background-position: 0 20%;}

  h1,h2 {
    body:not([class*="index"]) & {@extend .h1;}
  }

  p.lead {margin-right: 0; margin-left: 0; max-width: none;}

  @include bp-s-less {p.cta label.button {display: block;}}
}

// home intro

div.home-intro {
  @extend .page-intro;
  overflow: hidden;
  position: relative;
  padding-bottom: $spacing*11;
  background: white;

  @include bp-m {padding-bottom: $spacing*14;}
  @include bp-l {padding-bottom: $spacing*17;}

  h1 {
    position: relative;
    z-index: 10;
    margin-bottom: $vmargin;

    @include bp-s-less {font-size: $fs-25;}
    @include bp-s {margin-bottom: $vmargin*1.5;}
    @include bp-m {margin-bottom: $vmargin*2;}
    @include bp-l {margin-bottom: $vmargin*2.5;}

    span {display: block;}
  }

  div.intro-funnels {

    ol {
      display: flex;
      margin: 0 (-$gutter);
      @include bp-m {margin: 0;}

      li {
        flex: 1 1 $col-4;

        div.content {

          &:before {
            position: absolute;
            top: 0; left: 0; right: 0; bottom: 0;
            z-index: 1;
            @extend .aesthetic;
            background-color: white;
            background-repeat: no-repeat;
            background-position: 50%;
            background-size: cover;
            box-shadow: inset 0 50px 50px 0 white; // hides the hard nav line during transition
            content: "";
            opacity: 0;
            transition: opacity 1s;
          }

          &:after {
            position: absolute;
            top: 0; left: 0; right: 0; bottom: 0;
            z-index: 2;
            content: "";
            opacity: 0;
            transition: opacity 1s;
          }

          &.intro-funnel-1 {
            &:after {
              background-image: linear-gradient(to bottom, white 0%, rgba($clr-b2,0.75) 60%);
            }

            @include bp-m {
              label {
                border-radius: $border-radius 0 0 $border-radius;

                &:hover,
                &:focus {background: $clr-b2;}
              }
            }
          }

          &.intro-funnel-2 {
            &:after {background-image: linear-gradient(to bottom, white 0%, rgba($clr-b3,0.6) 60%);}

            @include bp-m {
              label {
                &:hover,
                &:focus {background: $clr-b3;}
              }
            }
          }

          &.intro-funnel-3 {
            &:after {
              background-image: linear-gradient(to bottom, white 0%, rgba($clr-b4,0.6) 60%);
            }

            @include bp-m {
              label {
                border-radius: 0 $border-radius $border-radius 0;

                &:hover,
                &:focus {background: $clr-b4;}
              }
            }
          }

          label {
            overflow: hidden;
            position: relative;
            z-index: 10;
            display: block;
            padding: ($spacing/2) 0;
            background: rgba(white,0.7);
            font-size: $fs-18;
            font-weight: 300;
            cursor: pointer;
            user-select: none;
            -webkit-user-select: none;
            transition: background-color 0.7s, color 0.35s;

            @include bp-s {font-size: $fs-25;}

            @include bp-m {
              padding: $spacing 0;
              font-size: $fs-35;

              &:hover,
              &:focus {
                background: $clr-b1;
                color: white;
              }
            }

            @include bp-l {font-size: $fs-40;}
          }

          div.text {
            position: absolute;
            left: 0; right: 0;
            z-index: 10;
            margin-top: $vmargin;
            padding: 0 $spacing;
            opacity: 0;
            visibility: hidden;
            transition: opacity 0.25s, visibility 0s 0.25s;

            @include bp-s {margin-top: $vmargin*1.5;}
            @include bp-m {margin-top: $vmargin*2;}
            @include bp-l {margin-top: $vmargin*2.5;}

            h2 {
              margin: 0 auto $vmargin;
              max-width: 970px;
              font-weight: 300;
              font-size: $fs-20;
              line-height: $lh-general;
              color: white;

              @include bp-s {font-size: $fs-25;}

              @include bp-m {
                margin-bottom: $vmargin*2;
                font-size: $fs-30;
              }

              @include bp-l {font-size: $fs-40;}
            }

            p {
              margin: 0 auto;
              
              a.button {
                display: block;
                @include bp-s {display: inline-block;}
                @include bp-m {font-size: $fs-30;}

                span:before {
                  @include bp-s-less {display: none;}
                }
              }
            }
          }
        }
      }
    }

    ol li input {
      @include hide-screen();

      &:checked + div.content {

        &:before {opacity: 1;}
        &:after {opacity: 1;}

        label {
          background: $clr-2;
          color: white;
          transition: background-color 1s, color 1s;

          + div.text {
            opacity: 1;
            visibility: visible;
            transition: opacity 0.75s 0.25s, visibility 0s 0.25s;
          }
        }
      }
    }
  }
}

// article pages

body.article {
  article {
    figure {border-bottom: 1px solid white;}

    div.strip div.wrap {
      h1 {@extend .h2-ext;}
      > p:nth-of-type(1):not([class]) {@extend .lead;}
      > ul {@extend .disc;}
      > ol {@extend .decimal;}
    }
  }
}

// guide pages

div.guide-content {

  @include bp-m {
    position: relative;
    z-index: 2;
    margin: -1.5rem 0 0;
    padding: 0 0 $vmargin;
    background: transparent;
  }

  div.wrap > div {

    @include bp-m {
      border-radius: 0.25rem;
      padding: ($spacing*2);
      background: white;
      box-shadow: 0 0 4px 2px rgba(black,0.1);
    }

    > ul:not([class]) {@extend .disc;}
    > ol:not([class]) {@extend .decimal;}
    > :last-child {margin-bottom: 0;}

    @include bp-m-less {
      p.back-top {@include hide-screen();}
    }
  }
}

// event pages

div.event-content {
  //> p:nth-of-type(1):not([class]) {@extend .lead;}
  > ul:not([class]) {@extend .checked;}
  > ol:not([class]) {@extend .numbered-blocks;}
}

// general wrap for lists

div.general-wrap {
  overflow: hidden;
  margin-bottom: $vmargin;
  border-radius: $border-radius;

  > ol,
  > ul {
    margin-bottom: 0;

    > li {
      margin-top: 4px;
      &:nth-child(1) {margin-top: 0;}

      &.col-4,
      &.col-6 {
        @include bp-m {&:nth-child(2) {margin-top: 0;}}
      }

      &.col-4 {
        @include bp-m {&:nth-child(3) {margin-top: 0;}}
      }
    }
  }

  div.wrap > h2 + p.lead + & {
    margin-top: $vmargin*1.5;
    @include bp-m {margin-top: $vmargin*2;}
  }
}

// announcement

div.announcement {
  padding: ($spacing/1.5) 0;
  background: $clr-2;
  color: white;

  @include bp-m {text-align: center;}

  div.wrap > :last-child {margin-bottom: 0;}

  p strong {
    display: block;
    @include bp-m {display: inline;}
  }

  a {
    font-weight: 700;
    color: white;
  }
}

// promotions

div.promotion {
  padding: ($spacing/1.5) 0;
  background: $clr-b1;
  color: white;

  @include bp-m {text-align: center;}

  div.wrap > :last-child {margin-bottom: 0;}

  p strong {
    display: block;
    @include bp-m {display: inline;}
  }

  a {color: white;}
}

// notices

p.notice {
  position: relative;
  border: 3px solid $clr-3;
  border-radius: $border-radius;
  padding: 1rem;
  background: white;

  &:before {
    position: absolute;
    left: 0.8rem; top: 1.05rem;
    font-size: $fs-24;
  }

  &.error,
  &.success,
  &.warning,
  &.caution {padding-left: 2.5rem;}

  &.error {
    border-color: $clr-error;
    background: white;

    &:before {
      @extend .icon-extension,.icon-exclamation-circle;
      color: $clr-error;
    }
  }

  &.success {
    border-color: $clr-success;

    &:before {
      @extend .icon-extension,.icon-check-circle;
      color: $clr-success;
    }
  }

  &.offer {
    border-color: $clr-b1;
    background: rgba($clr-b1,0.05);
    text-align: center;

    &:before {content: none;}

    div.buy-details & {
      border-color: white;
      background: rgba(white,0.1);

      span {
        display: block;
        font-size: $fs-18;
      }

      b {font-size: $fs-22;}
    }
  }

  &.warning {
    border-color: $clr-warning;

    &:before {
      @extend .icon-extension,.icon-exclamation-triangle;
    }
  }

  &.caution {
    &:before {
      @extend .icon-extension,.icon-exclamation-triangle;
    }
  }
}

// contact form inline

input#contact-form-inline {
  @include hide-screen();

  &:checked {
    + div.row {
      label.contact-form-inline-cta span[class*="icon-"]:before {@extend .icon-minus-circle;}
      + div.contact-form-inline {display: block;}
    }
  }
}

div.contact-form-inline {
  display: none;
  margin: $vmargin auto 0;
  border-radius: $border-radius;
  padding: $spacing;
  max-width: 30rem;
  background: $clr-1;

  form button.button {
    @include bp-s-less {width: 100%;}
  }
}

// guide navigation

input.guide-nav-toggle {
  position: fixed;
  top: 50%; left: -1000em;
  z-index: -1;
  visibility: hidden;

  &:checked + nav.guide-nav {

    @include bp-m {left: 0;}

    label.cta {
      span.button {
        box-shadow: inset 0 1px 2px rgba(0,0,0,0.75), 0 0 0 5px rgba(white,0.1);
        &:before {@extend .icon-times;}
      }

      &:after {@include bp-m {content: "";}}
    }

    div.guide-links {
      right: 0; left: 0;

      @include bp-m {
        right: auto; left: auto;
        box-shadow: 0.25rem 0 1.5rem rgba(black,0.3);
      }
    }

    + article div.guide-intro p.cta label span[class*="icon-"]:before,
    + article div.guide-content div.guide-nav-end label span[class*="icon-"]:before {@extend .icon-times;}
  }
}

nav.guide-nav {
  position: fixed;
  right: 0; bottom: 0;
  padding-bottom: 1rem;
  z-index: 1000;

  @include bp-m {
    right: auto; top: 0; left: -27rem;
    padding-bottom: 0; 
    width: 27rem;
    display: flex;
    align-items: stretch;
    flex-direction: column;
    transition: left 0.25s;
  }

  label.cta {
    cursor: pointer;
      
    span.button {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      z-index: 2;
      margin-right: 1rem;
      border-radius: 3rem;
      padding: 0!important;
      width: 2.5rem;
      height: 2.5rem;

      &:before {
        margin: 0!important;
        font-size: $fs-25;
      }

      @include bp-m {
        position: absolute;
        bottom: 0; left: 100%;
        margin: 0 0 1rem 1rem;
        width: 3rem;
        height: 3rem;

        &:before {font-size: $fs-30;}
      }

      span {@include hide-screen();}
    }

    &:after {
      position: fixed;
      left: 0; right: 0; bottom: 0; top: 0;
      z-index: 1;
      background: rgba(black,0.3);
    }
  }

  div.guide-links {
    position: fixed;
    left: -1000em; top: 0; bottom: 0;
    z-index: 1;
    background: $clr-1;
    text-align: center;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;

    @include bp-m {
      flex: 1;
      position: relative;
      left: auto; top: auto; bottom: auto;
      box-shadow: 0 0 0 rgba(black,0.3);
      transition: box-shadow 0.25s;
    }

    h2 {
      @extend .h4;
      margin: 0;
      padding: 0.75rem 1rem;
      background: rgba(black,0.3);
      color: white;
      font-weight: 300;

      @include bp-m {text-align: left;}
    }

    > ul {
      display: inline-block;
      margin-bottom: 0;
      padding: 0.75rem 0.5rem 2rem 1px;
      font-size: $fs-18;
      text-align: left;

      @include bp-m {
        display: block;
        font-size: $fs-20;
      }

      ul {
        margin-bottom: 0;

        li {
          margin-left: 1.3rem;
          border-left: 1px solid rgba(white,0.2);
        }
      }

      > li {

        > ul {
          padding-left: 0.5rem;
          > li {margin-left: 0.55rem;}
        }

        > a { 
          padding-left: 1rem;
          font-weight: 500;

          &:before {content: none;}
        }
      }

      a {
        display: flex;
        border-radius: 0 0.25rem 0.25rem 0;
        padding: 0.2rem 0.5rem;
        padding-left: 0;
        text-decoration: none;
        color: white;
        
        @include bp-m {
          &:hover,
          &:focus {background: rgba(white,0.075);}
        }

        &.current {
          background: rgba(black,0.3);
          box-shadow: -1px 0 0 $clr-b1, inset 4px 0 0 $clr-b1;
          &:before {color: $clr-b1;}
        }

        &:before {
          flex: 0 1 auto;
          color: rgba(white,0.2);
          content: "\02014\000A0";
        }

        span {flex: 1 1 auto;}
      }
    }
  }
}

// guide funnels

div.guide-funnel {
  overflow: hidden;
  position: relative;
  margin: 0 (-$gutter) $vmargin;
  background: $clr-b1;
  color: white;

  &.style-2 {background: $clr-b2;}
  &.style-3 {background: $clr-b3;}
  &.style-4 {background: $clr-b4;}

  @include bp-s {
    margin: 0 0 $vmargin;
    border-radius: $border-radius;
  }

  @include bp-m {
    float: right;
    margin: 0 0 $vmargin $spacing;
  }

  &:before {
    position: absolute;
    top: 0; left: 0; right: 0; bottom: 0;
    z-index: 1;
    @extend .aesthetic;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover;
    content: "";
    opacity: 0.25;

    @include bp-m {background-size: 200%;}
  }

  &.small {

    a {
      display: block;
      position: relative;
      z-index: 2;
      padding: $spacing 0.5rem;
      color: white;
      text-decoration: none;
      text-align: center;
      transition: background 0.2s;

      @include bp-m {
        padding: $spacing;
        text-align: left;
        font-size: $fs-24;

        &:hover,
        &:focus {background: rgba(black,0.1);}
      }

      b {
        display: block;
        margin-top: 0.25rem;
        font-weight: 500;

        @include bp-m {
          display: flex;
          align-items: center;
          font-size: $fs-27;
        }

        &:before {
          display: block;
          font-size: $fs-35;

          @include bp-m {margin-right: 0.25em;}
        }
      }
    }
  }

  &.medium {
    padding: $spacing 0.5rem;
    text-align: center;

    @include bp-m {
      padding: $spacing;
      max-width: 22rem;
    }

    div.content {
      position: relative;
      z-index: 2;

      h3 {margin-bottom: 0.75rem;}

      p {
        margin-bottom: 1rem;

        a.button {
          font-size: $fs-20;
          @include bp-s-less {display: block;}
        }
      }

      > :last-child {margin-bottom: 0;}
    }
  }
}

// guide service funnel

div.guide-service-funnel {
  position: relative;
  z-index: 1;
  overflow: hidden;
  margin: 0 (-$gutter);

  @include bp-m {
    margin: 0 0 $vmargin;
    border-radius: 0.25rem;
  }

  h2 {
    @extend .h3;
    margin: 0;
    padding: 1rem 1.5rem;
    background: $clr-2;
    color: white;
    //@include hide-screen();
  }

  div.content {
    position: relative;
    padding: $spacing $spacing 0;
    background: $clr-b2;
    color: white;

    @include bp-m {
      padding: $spacing;
      padding-left: ($spacing*2);
    }

    &:before {
      position: absolute;
      top: 0; left: 0; right: 0; bottom: 0;
      z-index: 1;
      @extend .aesthetic;
      background-repeat: no-repeat;
      background-position: 50%;
      background-size: cover;
      content: "";
      opacity: 0.3;
    }

    &:after {
      position: absolute;
      top: 0; left: 0; right: 0; bottom: 0;
      z-index: 1;
      background-image: linear-gradient(to bottom, rgba(black,0.15) 0%, transparent 60%);
      content: "";
    }

    div.row.text {
      position: relative;
      z-index: 2;

      @include bp-m {align-items: center;}

      h3 {
        @extend .h2-ext;
        margin-bottom: $vmargin/2;
        font-weight: 300;
      }

      p.lead {
        @include bp-m {margin-bottom: 0;}
      }

      div.features {
        margin: 0 (-$gutter);
        padding: ($spacing*1.5) $spacing;
        background: rgba(black,0.3);
        text-align: center;

        @include bp-m {
          margin: 0;
          border-radius: $border-radius;
          padding: $spacing*1.25;
        }

        ul {
          display: inline-block;
          text-align: left;
          @include bp-m {font-size: $fs-24;}
        }

        p {
          margin: 0;
          text-align: center;

          a.button {
            @include bp-s-less {display: block;}
            > span > span > span {@include hide-screen();}
          }
        }
      }
    }
  }
}

// supporting info - on guide and article pages

div.supporting-info {
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
  margin: 0 0 $vmargin;
  border-bottom: 5px solid $clr-4;
  padding: 0.5rem 0;

  ul, p {margin: 0.25rem 0;}

  &.end {
    margin-bottom: 0;
    border-bottom: 0;
    border-top: 5px solid $clr-4;
    background: none;

    div.guide-nav-end + & {border-top-color: $clr-3;}
  }

  ul.share-options {flex: 0 0 auto;}

  ul.general {
    flex: 1 0 auto;
    list-style: none;
    font-size: $fs-16;

    @include bp-m {display: flex;}

    li {
      display: flex;
      align-items: center;
      margin-right: 1rem;

      &:before {margin-right: 0.25rem;}

      a {
        text-decoration: none;
        font-weight: 600;
        color: $clr-1;

        @include bp-m {
          &:hover,
          &:focus {color: $clr-b1;}
        }
      }
    }
  }

  p.top {
    flex: 1 0 auto;

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 2rem;
      width: 1.7rem;
      height: 1.7rem;
      background: $clr-b1;
      text-decoration: none;
      text-align: center;
      color: white;
      transition: box-shadow 0.1s;

      @include bp-m {
        &:hover,
        &:focus {
          box-shadow: inset 0 0 0 4rem rgba(black,0.1);
        }
      }

      span {@include hide-screen();}
    }
  }
}

// guide nav end

div.guide-nav-end {
  clear: both;
  border-radius: 0.25rem 0.25rem 0 0;
  padding: 0.75rem;
  background: $clr-4;
  text-align: center;

  @include bp-s-less {
    label.button {display: block;}
  }
}

// pdf embed

div.sample-preview {
  display: flex;
  align-items: stretch;
}

div.pdf-insert {
  flex: 1;
  position: relative;
  border-bottom: 5px solid #4F5356;
  background: #4F5356;
  box-shadow: 0 0 10px 5px rgba(black,0.1);

  @-moz-document url-prefix() {
    border-bottom: 20px solid #2A2A2E;
    background: #2A2A2E;
  }
  
  @include bp-m-less {display: none;}

  object {
    position: absolute;
    top: 0; left: 0;
    border: 0;
    width: 100%;
    height: 100%;
    background: rgba(white,0.3);
  }
}

p.pdf-legacy {
  padding: ($spacing*2);
  text-align: center;
}

// video insert

div.video-insert {
  position: relative;

  &.playing {
    &:before {
      display: block;
      padding-top: 56.23%;
      content: "";
    }
  }

  &.playing,
  a.cta {
    margin: 0 (-$gutter) $vmargin;
    background: black;

    @include bp-m {
      margin: 0 auto $vmargin;
      max-width: 600px;
      box-shadow: 0 0 10px 5px rgba(black,0.1);
    }

    div.page-intro &,
    body.landing & {
      margin-top: ($vmargin*1.5);
      margin-bottom: 0;
      @include bp-m {margin-top: $vmargin*2;}
    }
  }

  a.cta {
    position: relative;
    display: block;
    transition: filter 0.2s;

    @include bp-m {
      &:hover,
      &:focus {filter: brightness(90%);}
    }

    &:before {
      position: relative;
      z-index: 2;
      display: block;
      padding-top: 56.23%;
      background: url(../../assets/images/content/other/play.png) no-repeat 50%;
      cursor: pointer;
      content: "";
    }

    img {
      position: absolute;
      right: 0; top: 0; left: 0; bottom: 0;
      z-index: 1;
      width: 100%;
      cursor: pointer;
    }
  }

  object {
    position: absolute;
    top: 0; left: 0;
    border: 0;
    width: 100%;
    height: 100%;
    background: transparent;
  }
}

// contact button

input.contact-button-toggle {
  position: fixed;
  top: 50%; left: -1000em;
  z-index: -1;
  visibility: hidden;

  &:checked + div.contact-button {

    label.cta {
      display: block;

      span.button {
        box-shadow: inset 0 1px 2px rgba(black,0.75), 0 0 0 5px rgba(white,0.1);
        &:before {@extend .icon-times;}
      }

      &:after {@include bp-s {content: "";}}
    }

    div.form {
      top: 0; right: 0;

      @include bp-s-less {left: 0;}

      @include bp-s {
        top: auto;
        margin-bottom: 5rem;
        opacity: 1;
        transition: margin-bottom 0.25s, opacity 0.25s;
      }
    }
  }
}

div.contact-button {
  position: fixed;
  bottom: 0; right: 0;
  z-index: 1000;
  padding-bottom: 1rem;

  label.cta {
    display: none;
    cursor: pointer;

    @include bp-s {display: block;}

    span.button {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      z-index: 2;
      margin-right: 1rem;
      border-radius: 3rem;
      padding: 0!important;
      width: 2.5rem;
      height: 2.5rem;

      &:before {
        margin: 0!important;
        font-size: $fs-25;
      }

      @include bp-m {
        width: 3rem;
        height: 3rem;

        &:before {font-size: $fs-30;}
      }


      span {@include hide-screen();}
    }

    &:after {
      position: fixed;
      left: 0; right: 0; bottom: 0; top: 0;
      z-index: 1;
      background: rgba(black,0.3);
    }
  }

  div.form {
    position: fixed;
    bottom: 0; right: 1000em;
    z-index: 1;
    background: $clr-4;

    @include bp-s {
      margin: 0 1rem 4rem 0;
      border-radius: $border-radius;
      width: 300px;
      background: none;
      opacity: 0;
      box-shadow: 0.25rem 0.25rem 1.5rem rgba(black,0.3);
      transition: margin-bottom 0.25s, opacity 0.25s, right 0s 0.25s;
    }

    h2 {
      @extend .h3;
      position: relative;
      margin: 0;
      background: $clr-2;
      color: white;
      padding: 0.5rem 1rem;

      @include bp-s {border-radius: $border-radius $border-radius 0 0;}

      &:after {
        position: absolute;
        right: 1rem;
        top: 0;
        border-radius: 3rem;
        width: 3rem;
        height: 3rem;
        background: white url(../../assets/images/content/other/contact-button-person.jpg) no-repeat 50%;
        background-size: cover;
        box-shadow: 0 0 0 5px $clr-2;
        content: "";

        @include bp-s {top: -0.15rem;}
      }
    }

    form {
      padding: 1rem;
      background: $clr-4;

      @include bp-s {border-radius: 0 0 $border-radius $border-radius;}

      div.form-group {
        margin: 0 0 ($vmargin/2);

        &.linear-fields {
          margin-bottom: 0;

          label {
            margin-top: 0;
            margin-bottom: ($vmargin/2);
          }
        }

        input:not([type=checkbox]):not([type=radio]):not([type=submit]):not([type=file]),
        textarea,
        .custom-select {
          padding: 0.4rem 0.5rem;
          font-size: $fs-18;
        }
      }

      button {
        width: 100%;
        font-size: $fs-20;
      }
    }
  }
}

// medical professional on about page

div.med-pro {
  position: relative;
  overflow: hidden;
  margin-bottom: $vmargin;
  border-radius: $border-radius;
  background: $clr-4;
  text-align: left;

  @include bp-m {text-align: right;}

  &:before {
    display: block;
    height: 15rem;
    background: $clr-3 url(../../assets/images/content/other/medical-professionals.jpg) no-repeat 50%;
    background-size: cover;
    content: "";

    @include bp-m {
      position: absolute;
      top: 0; left: 0; bottom: 0;
      border-right: 4px solid white;
      width: 50%;
      height: auto;
    }
  }

  p {
    @extend .lead;
    margin-bottom: 0;
    padding: 1rem;
    text-align: left;

    @include bp-s {padding: 2rem;}
    
    @include bp-m {
      display: inline-block;
      padding: 4rem 2rem;
      width: 50%;
    }
  }
}

// mdr template

div.mdr-template {
  div.template-cta {order: 2;}
}

// buy details

div.buy-details {

  div.purchase-info {
    overflow: hidden;
    position: relative;
    margin: 0 (-$gutter) $vmargin;
    background: $clr-2;
    text-align: center;
    color: white;

    body.consulting & {background: $clr-b2;}
    body.academy &,
    body.join & {background: $clr-b3;}
    body.resources & {background: $clr-b4;}

    @include bp-s {
      margin: 0 0 $vmargin;
      border-radius: $border-radius;
    }

    &:before {
      position: absolute;
      top: 0; left: 0; right: 0; bottom: 0;
      z-index: 1;
      @extend .aesthetic;
      background-repeat: no-repeat;
      background-position: 50%;
      background-size: cover;
      content: "";
      opacity: 0.2;

      @include bp-m {background-size: 200%;}
    }

    div.content {
      position: relative;
      z-index: 2;
      padding: $spacing;
    }

    p.prices {
      margin-bottom: 0.5rem;

      span {display: block;}
      b {@include hide-screen();}

      span.was {
        font-size: $fs-18;

        span.price span {
          display: inline-block;
          text-decoration: line-through;
        }
      }

      span.current {
        span.price {
          font-size: $fs-30;
          line-height: 1.1;
        }

        span.vat {
          display: block;
          font-size: $fs-14;
        }

        span {
          @include bp-m {display: inline-block;}
        }
      }
    }

    ul.buttons {
      margin: 0;

      li {
        display: block;
        margin-right: 0;

        a.button {display: block;}
      }
    }

    p.cta {
      margin: 0;
      a.button {display: block;}
    }

    p.information {
      margin: 1rem 0 0;
      font-size: $fs-16;
    }
  }

  div.additional-info {
    border-radius: $border-radius;
    padding: $spacing;
    background: $clr-4;
    font-size: $fs-16;

    div.strip.style-5 & {background: rgba($clr-4,0.4);}

    p.list-lead {
      margin-bottom: 0.2rem;
      font-weight: 500;
    }

    ul.checked {
      margin-bottom: 0;
      
      li {margin: 0;}
    }

    ul.share-options {margin-top: $vmargin;}
  }
}

// testimonials

div.testimonials {
  margin: $vmargin 0;
  background: url(../../assets/images/content/other/world-map-mob.png) no-repeat 50%;
  background-size: auto 100%;

  @include bp-m {
    margin: ($vmargin*2) 0;
    background-image: url(../../assets/images/content/other/world-map.png);
  }

  ul {
    margin-bottom: 0;
    text-align: center;

    li {

      input {
        position: fixed;
        top: 50%; left: -1000em;
        z-index: -1;
        visibility: hidden;

        &:checked {
          + div.content {
            overflow: visible;
            visibility: visible;
            margin-left: 0;
            padding: 1.25rem 0;
            width: 100%;
            max-height: 30rem;
            opacity: 1;
            transition: opacity 0.25s 0.25s, margin-left 0.25s 0.25s, max-height 0s 0.25s, padding 0s 0.25s;

            @include bp-m {padding: 3rem 0;}
          }
        }
      }

      div.content {
        display: flex;
        justify-content: center;
        overflow: hidden;
        visibility: hidden;
        margin-left: -20rem;
        padding: 0;
        width: 0;
        max-height: 0;
        opacity: 0;
        transition: opacity 0.25s, margin-left 0.25s, visibility 0s 0.25s, width 0s 0.25s, max-height 0s 0.25s, padding 0s 0.25s;

        blockquote {
          position: relative;
          display: flex;
          flex-direction: column;
          justify-content: center;
          margin-bottom: 0;
          border-left: 5px solid $clr-b1;
          border-radius: $border-radius;
          padding: $spacing ($spacing/1.7);
          max-width: 650px;
          min-height: 20rem;
          background: rgba($clr-4,0.5);

          @include bp-s {
            padding: $spacing;
            min-height: 15rem;
          }

          @include bp-m {padding: $spacing ($spacing*1.5);}

          p {
            @extend .h4;

            &:before {content: "\0201C";}
            &:after {content: "\0201D";}
          }

          cite {
            font-style: normal;
            @include bp-m-less {font-size: $fs-18;}
            span {display: block;}
          }
        }

        label.button {
          display: flex;
          align-items: center;
          justify-content: center;
          position: absolute;
          right: 50%; top: 100%;
          z-index: 10;
          margin: -1.25rem -1.25rem 0 0;
          border-radius: 3rem;
          padding: 0!important;
          width: 2.5rem;
          height: 2.5rem;
          font-size: $fs-25;

          @include bp-m {
            right: 0; top: 50%;
            margin: -1.25rem -1.25rem 0 0;
          }

          &:before {
            @extend .icon-extension, .icon-angle-right;
          }

          span {@include hide-screen();}
        }
      }
    }
  }
}
